// Helper function to filter by subAccountId
export const filterBySubAccount = <T extends { subAccountId: string }>(
  items: T[],
  subAccountId: string
): T[] => {
  if (subAccountId === 'default-sub-account') {
    return items
  }
  return items.filter(item => item.subAccountId === subAccountId)
}
