// Products API v1
// GET /api/v1/revenue-engine/products - List products with pagination, search, filter
// POST /api/v1/revenue-engine/products - Create new product

import { NextRequest, NextResponse } from 'next/server'
import { mockProducts, filterBySubAccount } from '@/lib/revenue-engine/mock-data'

// GET - List products
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    
    // Query parameters
    const subAccountId = searchParams.get('subAccountId')
    const search = searchParams.get('search') || ''
    const status = searchParams.get('status')
    const productType = searchParams.get('productType')
    const page = parseInt(searchParams.get('page') || '1')
    const limit = parseInt(searchParams.get('limit') || '50')
    
    // Validation
    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }
    
    // Get products (mock data for now)
    let products = filterBySubAccount(mockProducts, subAccountId)
    
    // Apply search filter
    if (search) {
      const searchLower = search.toLowerCase()
      products = products.filter(p => 
        p.name.toLowerCase().includes(searchLower) ||
        p.description?.toLowerCase().includes(searchLower) ||
        p.sku?.toLowerCase().includes(searchLower)
      )
    }
    
    // Apply status filter
    if (status && status !== 'undefined' && status !== 'all') {
      products = products.filter(p => p.status === status)
    }
    
    // Apply product type filter
    if (productType && productType !== 'undefined' && productType !== 'all') {
      products = products.filter(p => p.type === productType)
    }
    
    return NextResponse.json({
      success: true,
      data: {
        products,
        pagination: {
          page,
          limit,
          total: products.length,
          totalPages: Math.ceil(products.length / limit),
        },
      },
    })
  } catch (error: any) {
    console.error('GET /api/v1/revenue-engine/products error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch products' },
      { status: 500 }
    )
  }
}

// POST - Create new product (mock implementation)
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const {
      subAccountId,
      name,
      description,
      sku,
      price,
      currency,
      productType,
      taxable,
      status,
    } = body
    
    // Validation
    if (!subAccountId || !name || price === undefined) {
      return NextResponse.json(
        {
          success: false,
          error: 'subAccountId, name, and price are required',
        },
        { status: 400 }
      )
    }
    
    // Validate price
    if (typeof price !== 'number' || price < 0) {
      return NextResponse.json(
        { success: false, error: 'Price must be a non-negative number' },
        { status: 400 }
      )
    }
    
    // Create mock product
    const product = {
      id: `prod-${Date.now()}`,
      name,
      description: description || null,
      sku: sku || `SKU-${Date.now()}`,
      price,
      currency: currency || 'USD',
      type: productType || 'service',
      status: status || 'active',
      taxable: taxable !== false,
      subAccountId,
      createdAt: new Date().toISOString(),
    }
    
    return NextResponse.json({
      success: true,
      data: { product },
      message: 'Product created successfully (mock data - database tables not yet created)',
    }, { status: 201 })
  } catch (error: any) {
    console.error('POST /api/v1/revenue-engine/products error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to create product' },
      { status: 500 }
    )
  }
}
